* {
  font-family: "Comfortaa";
}

pre {
  font-family: 'Cascadia Code', sans-serif;
}

body {
  margin: 0;
  margin-bottom: 20px;
  padding: 0;
  font-family: Arial, sans-serif;
  //background-image: url(https://img.freepik.com/free-vector/colored-points-connected-net_1048-12426.jpg?w=1480&t=st=1693330264~exp=1693330864~hmac=ef2df19047d9aa90700e2ec0a759ebad9da125c4d48ee446bd6312ece9ded497");
  background-image: url("https://img.freepik.com/free-vector/low-poly-digital-network-lines-connecting-mesh-background_1017-25547.jpg?w=2000&t=st=1693330149~exp=1693330749~hmac=930f4de40a87e7148c96a1ba5d6885315705eab55b404dec254379dec8d825c9");
  // background-image: url("https://images.pond5.com/network-nodes-connected-background-footage-089996011_prevstill.jpeg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  width: 100vw;
  height: 100vh;
}


header {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  align-items: center;
  padding: 0px 50px;
  margin: 0;
  font-family: "Comfortaa";
  font-size: large;
  text-decoration: white;
}

header>h1 {
  color: rgb(0, 0, 0);
  background-color: rgba(255, 255, 255, 0.5);
  text-shadow: 0 0 15px rgba(120, 162, 210, 0.688);
}

img,
button {
  cursor: pointer;
}


/* CSS */
.button-50 {
  appearance: button;
  background-color: #000;
  background-image: none;
  border: 1px solid #000;
  border-radius: 8px;
  box-shadow: #fff 4px 4px 0 0, #000 4px 4px 0 1px;
  box-sizing: border-box;
  color: #fff;
  cursor: pointer;
  display: inline-block;
  font-family: "Comfortaa";
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  margin: 5px 5px 10px 0;
  overflow: visible;
  padding: 12px 40px;
  text-align: center;
  text-transform: none;
  touch-action: manipulation;
  user-select: none;
  -webkit-user-select: none;
  vertical-align: middle;
  white-space: nowrap;
}

.button-50:focus {
  text-decoration: none;
}

.button-50:hover {
  text-decoration: none;
  transform: scale(1.1);
}

.button-50:active {
  box-shadow: rgba(0, 0, 0, .125) 0 3px 5px inset;
  outline: 0;
}

.button-50:not([disabled]):active {
  box-shadow: #fff 2px 2px 0 0, #000 2px 2px 0 1px;
  transform: scale(1.1);
}

@media (min-width: 768px) {
  .button-50 {
    padding: 12px 50px;
  }
}